	module.exports = {"data":{"postsJson":{"title":"Sign Six","questionsTitle":"OPENING THE WORD","display":"block","bookName":"John","ref":"9:1-10:42","html":"<p>   <strong>9</strong><u>1</u>As he passed by, he saw a man blind from birth. <u>2</u>His disciples asked him,</p><h1>THE DISCIPLES </h1><h3>Rabbi, who sinned, this man or his parents, that he was born blind?</h3><p>   <u>3</u>Jesus answered,</p><h1>JESUS</h1><h2>Neither did this man sin, nor his parents; but, that the works of God might be revealed in him. <u>4</u>I must work the works of him who sent me, while it is day. The night is coming, when no one can work. <u>5</u>While I am in the world, I am the light of the world.</h2><p><u>6</u>When he had said this, he spat on the ground, made mud with the saliva, anointed the blind man’s eyes with the mud, <u>7</u>and said to him,</p><h1>JESUS</h1><h2>Go, wash in the pool of Siloam</h2><p>(which means “Sent”). So he went away, washed, and came back seeing. <u>8</u>The neighbors therefore, and those who saw that he was blind before, said,</p><h1>A BLIND BEGGAR’S FRIENDS</h1><h4>Isn’t this he who sat and begged?</h4><p><u>9</u>Others were saying,</p><h1>A BLIND BEGGAR’S FRIENDS</h1><h4>It is he.</h4><p>Still others were saying,</p><h1>A BLIND BEGGAR’S FRIENDS</h1><h4>He looks like him.</h4><p>   He said,</p><h1>A BLIND BEGGAR</h1><h4>I am he.</h4><p><u>10</u>They therefore were asking him,</p><h1>A BLIND BEGGAR’S FRIENDS</h1><h4>How were your eyes opened?</h4><p>   <u>11</u>He answered,</p><h1>A BLIND BEGGAR</h1><h4>A man called Jesus made mud, anointed my eyes, and said to me, ‘Go to the pool of Siloam, and wash.’ So I went away and washed, and I received sight.</h4><p>   <u>12</u>Then they asked him,</p><h1>A BLIND BEGGAR’S FRIENDS</h1><h4>Where is he?</h4><p>   He said,</p><h1>A BLIND BEGGAR</h1><h4>I don’t know.</h4><p>   <u>13</u>They brought him who had been blind to the Pharisees. <u>14</u>It was a Sabbath when Jesus made the mud and opened his eyes. <u>15</u>Again therefore the Pharisees also asked him how he received his sight. He said to them,</p><h1>A BLIND BEGGAR</h1><h4>He put mud on my eyes, I washed, and I see.</h4><p>   <u>16</u>Some therefore of the Pharisees said,</p><h1>THE PHARISEES</h1><h4>This man is not from God, because he doesn’t keep the Sabbath.</h4><p>Others said,</p><h1>THE PHARISEES</h1><h4>How can a man who is a sinner do such signs?</h4><p>There was division among them. <u>17</u>Therefore they asked the blind man again,</p><h1>THE PHARISEES</h1><h4>What do you say about him, because he opened your eyes?</h4><p>   He said,</p><h1>A BLIND BEGGAR</h1><h4>He is a prophet.</h4><p>   <u>18</u>The Jews therefore did not believe concerning him, that he had been blind, and had received his sight, until they called the parents of him who had received his sight, <u>19</u>and asked them,</p><h1>JEWISH LEADERS</h1><h4>Is this your son, whom you say was born blind? How then does he now see?</h4><p>   <u>20</u>His parents answered them,</p><h1>A BLIND BEGGAR’S PARENTS</h1><h4>We know that this is our son, and that he was born blind; <u>21</u>but how he now sees, we don’t know; or who opened his eyes, we don’t know. He is of age. Ask him. He will speak for himself.</h4><p><u>22</u>His parents said these things because they feared the Jews; for the Jews had already agreed that if any man would confess him as Christ, he would be put out of the synagogue. <u>23</u>Therefore his parents said, “He is of age. Ask him.” <br />   <u>24</u>So they called the man who was blind a second time, and said to him,</p><h1>JEWISH LEADERS</h1><h4>Give glory to God. We know that this man is a sinner.</h4><p>   <u>25</u>He therefore answered,</p><h1>A BLIND BEGGAR</h1><h4>I don’t know if he is a sinner. One thing I do know: that though I was blind, now I see.</h4><p>   <u>26</u>They said to him again,</p><h1>JEWISH LEADERS</h1><h4>What did he do to you? How did he open your eyes?</h4><p>   <u>27</u>He answered them,</p><h1>A BLIND BEGGAR</h1><h4>I told you already, and you didn’t listen. Why do you want to hear it again? You don’t also want to become his disciples, do you?</h4><p>   <u>28</u>They insulted him and said,</p><h1>JEWISH LEADERS</h1><h4>You are his disciple, but we are disciples of Moses. <u>29</u>We know that God has spoken to Moses. But as for this man, we don’t know where he comes from.</h4><p>   <u>30</u>The man answered them,</p><h1>A BLIND BEGGAR</h1><h4>How amazing! You don’t know where he comes from, yet he opened my eyes. <u>31</u>We know that God doesn’t listen to sinners, but if anyone is a worshiper of God, and does his will, he listens to him.* <u>32</u>Since the world began it has never been heard of that anyone opened the eyes of someone born blind. <u>33</u>If this man were not from God, he could do nothing.</h4><p>   <u>34</u>They answered him,</p><h1>JEWISH LEADERS</h1><h4>You were altogether born in sins, and do you teach us?</h4><p>They threw him out. <br />   <u>35</u>Jesus heard that they had thrown him out, and finding him, he said,</p><h1>JESUS</h1><h2>Do you believe in the Son of God?</h2><p>   <u>36</u>He answered,</p><h1>A BLIND BEGGAR</h1><h4>Who is he, Lord, that I may believe in him?</h4><p>   <u>37</u>Jesus said to him,</p><h1>JESUS</h1><h2>You have both seen him, and it is he who speaks with you.</h2><p>   <u>38</u>He said,</p><h1>A BLIND BEGGAR</h1><h4>Lord, I believe!</h4><p>and he worshiped him. <br />   <u>39</u>Jesus said,</p><h1>JESUS</h1><h2>I came into this world for judgment, that those who don’t see may see; and that those who see may become blind.</h2><p>   <u>40</u>Those of the Pharisees who were with him heard these things, and said to him,</p><h1>THE PHARISEES</h1><h4>Are we also blind?</h4><p>   <u>41</u>Jesus said to them,</p><h1>JESUS</h1><h2>If you were blind, you would have no sin; but now you say, ‘We see.’ Therefore your sin remains. <br />   <strong>10</strong><u>1</u>“Most certainly, I tell you, one who doesn’t enter by the door into the sheep fold, but climbs up some other way, the same is a thief and a robber. <u>2</u>But one who enters in by the door is the shepherd of the sheep. <u>3</u>The gatekeeper opens the gate for him, and the sheep listen to his voice. He calls his own sheep by name, and leads them out. <u>4</u>Whenever he brings out his own sheep, he goes before them, and the sheep follow him, for they know his voice. <u>5</u>They will by no means follow a stranger, but will flee from him; for they don’t know the voice of strangers.</h2><p><u>6</u>Jesus spoke this parable to them, but they didn’t understand what he was telling them. <br />   <u>7</u>Jesus therefore said to them again,</p><h1>JESUS</h1><h2>Most certainly, I tell you, I am the sheep’s door. <u>8</u>All who came before me are thieves and robbers, but the sheep didn’t listen to them. <u>9</u>I am the door. If anyone enters in by me, he will be saved, and will go in and go out, and will find pasture. <u>10</u>The thief only comes to steal, kill, and destroy. I came that they may have life, and may have it abundantly. <u>11</u>I am the good shepherd.* The good shepherd lays down his life for the sheep. <u>12</u>He who is a hired hand, and not a shepherd, who doesn’t own the sheep, sees the wolf coming, leaves the sheep, and flees. The wolf snatches the sheep, and scatters them. <u>13</u>The hired hand flees because he is a hired hand, and doesn’t care for the sheep. <u>14</u>I am the good shepherd. I know my own, and I’m known by my own; <u>15</u>even as the Father knows me, and I know the Father. I lay down my life for the sheep. <u>16</u>I have other sheep, which are not of this fold.* I must bring them also, and they will hear my voice. They will become one flock with one shepherd. <u>17</u>Therefore the Father loves me, because I lay down my life,* that I may take it again. <u>18</u>No one takes it away from me, but I lay it down by myself. I have power to lay it down, and I have power to take it again. I received this commandment from my Father.</h2><p>   <u>19</u>Therefore a division arose again among the Jews because of these words. <u>20</u>Many of them said,</p><h1>THE CROWD</h1><h4>He has a demon, and is insane! Why do you listen to him?</h4><p><u>21</u>Others said,</p><h1>THE CROWD</h1><h4>These are not the sayings of one possessed by a demon. It isn’t possible for a demon to open the eyes of the blind, is it?*</h4><p>   <u>22</u>It was the Feast of the Dedication* at Jerusalem. <u>23</u>It was winter, and Jesus was walking in the temple, in Solomon’s porch. <u>24</u>The Jews therefore came around him and said to him,</p><h1>JEWISH LEADERS</h1><h4>How long will you hold us in suspense? If you are the Christ, tell us plainly.</h4><p>   <u>25</u>Jesus answered them,</p><h1>JESUS</h1><h2>I told you, and you don’t believe. The works that I do in my Father’s name, these testify about me. <u>26</u>But you don’t believe, because you are not of my sheep, as I told you. <u>27</u>My sheep hear my voice, and I know them, and they follow me. <u>28</u>I give eternal life to them. They will never perish, and no one will snatch them out of my hand. <u>29</u>My Father, who has given them to me, is greater than all. No one is able to snatch them out of my Father’s hand. <u>30</u>I and the Father are one.</h2><p>   <u>31</u>Therefore Jews took up stones again to stone him. <u>32</u>Jesus answered them,</p><h1>JESUS</h1><h2>I have shown you many good works from my Father. For which of those works do you stone me?</h2><p>   <u>33</u>The Jews answered him,</p><h1>JEWISH LEADERS</h1><h4>We don’t stone you for a good work, but for blasphemy: because you, being a man, make yourself God.</h4><p>   <u>34</u>Jesus answered them,</p><h1>JESUS</h1><h2>Isn’t it written in your law, ‘I said, you are gods?’* <u>35</u>If he called them gods, to whom the word of God came (and the Scripture can’t be broken), <u>36</u>do you say of him whom the Father sanctified and sent into the world, ‘You blaspheme,’ because I said, ‘I am the Son of God?’ <u>37</u>If I don’t do the works of my Father, don’t believe me. <u>38</u>But if I do them, though you don’t believe me, believe the works; that you may know and believe that the Father is in me, and I in the Father.</h2><p>   <u>39</u>They sought again to seize him, and he went out of their hand. <u>40</u>He went away again beyond the Jordan into the place where John was baptizing at first, and there he stayed. <u>41</u>Many came to him. They said,</p><h1>THE CROWD</h1><h4>John indeed did no sign, but everything that John said about this man is true.</h4><p><u>42</u>Many believed in him there.</p><h5>*John 9:31 Psalm 66:18, Proverbs 15:29; 28:9 <br />*John 10:11 Isaiah 40:11; Ezekiel 34:11-12,15,22 <br />*John 10:16 Isaiah 56:8 <br />*John 10:17 Isaiah 53:7-8 <br />*John 10:21 Exodus 4:11 <br />*John 10:22 The “Feast of the Dedication” is the Greek name for “Hanukkah”, a celebration of the rededication of the Temple. <br />*John 10:34 Psalm 82:6</h5>","path":"/345","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?","prevPath":"/344","nextPath":"/346"}},"pathContext":{}}

